import * as React from 'react';
import { GeneralType, Functions, WsMessage } from 'src/Types';
import {Launcher, Message} from 'react-chat-window'
import { useState, useEffect } from 'react';
import { sendWsMessage, sendWsHiMessage } from 'src/utils/socket';
import { ServerEndpoint } from 'src/utils/misc';
import ReconnectingWebSocket from 'reconnecting-websocket'

export interface Props {
  general: GeneralType
  functions: Functions
}
const HOST = ServerEndpoint.replace(/^http/, 'ws')

function Chat({ general, functions }: Props) {
  const [messages, setMessages] = useState<Message[]>([])
  const [isChatPresent, setChatPresent] = useState(false)
  const [isChatOpen, setChatOpen] = useState(true)
  const [lastingSocket, setLastingSocket] = useState<ReconnectingWebSocket | undefined>()

  const messageSent = (message: Message) => {
    if (!lastingSocket) {return }
    sendWsMessage(lastingSocket, message)
    setMessages(oldMessages => [...oldMessages, message])
  }

  useEffect(()=>{

    const socket = new ReconnectingWebSocket(HOST)
    setLastingSocket(socket)
    socket.addEventListener('open', () => {
      sendWsHiMessage(socket)
    })

    socket.onmessage = (event) => {
      let wsMessage = JSON.parse(event.data) as WsMessage
      let payload = wsMessage.payload
      if (payload.type === 'action' &&  payload.action=== 'showChat') {
        setChatPresent(true)
      }
      if (payload.type === 'message') {
        let message = payload.message
        message.author = 'Alejandro'
        setMessages( oldMessages =>[ ...oldMessages, message])
      }
    }
  }, [])
  return (
    <div>
      { isChatPresent && (
        <Launcher
          agentProfile={{
            teamName: 'Chat with the Author',
          }}
          onMessageWasSent={(message: Message) => messageSent(message)}
          messageList={messages}
          showEmoji={true}
          isOpen={isChatOpen}
          handleClick={()=> setChatOpen(!isChatOpen)}
        />
      )}
    </div>
  )
}

export default Chat




