import { ensureSessionId } from "./misc"
import { Message } from 'react-chat-window'
import { WsMessage } from 'src/Types'
import ReconnectingWebSocket from 'reconnecting-websocket'

const WsCheck = '2kjn427w 45qe83'


export const sendWsMessage = (socket: ReconnectingWebSocket, message: Message) => {
  let wsMessge : WsMessage = {
    sessionId: ensureSessionId(),
    check: WsCheck,
    payload: { type:'message', message},
  }
  socket.send(JSON.stringify(wsMessge))
}
export const sendWsHiMessage = (socket: ReconnectingWebSocket) => {
  let wsMessge : WsMessage = {
    sessionId: ensureSessionId(),
    check: WsCheck,
    payload: {type:'action', action:'hi'},
  }
  socket.send(JSON.stringify(wsMessge))
}



